    //     this._mainWorker = workerInstance;
    //     this._mainWorker.postMessage({
    //         type: WORKER_MESSAGE_TYPE.ATTACH_CANVAS,
    //         maincanvas: this.transferControl
    //     }, [this.transferControl])

    //     // this._mainWorker.onmessage = function(e) {
