    // captureStream을 Manual하게 제어하는 함수.
    // mixedStream의 captureStream(0) 으로 세팅해놔야 requestFrame()이 동작함.
    captureFrame(time) {
        if (time - lastFrameTime < FRAME_MIN_TIME) { //skip the frame if the call is too early
            window.requestAnimationFrame(this.captureFrame.bind(this));
            return; // return as there is nothing to do
        }
        lastFrameTime = time; // remember the time of the rendered frame
        // OffscreenCanvas가 있으면 frontCanvas로 전체 이미지를 copy하는 과정이 필요
        // 현재 Chrome에만 OffscreenCanvas가 지원되기 떄문에 검증작업 필요(2019-11-20 기만)
        if(typeof(OffscreenCanvas))
            this.frontCanvasCtx.drawImage(this.canvas, 0, 0);
        this.mixedStream.getVideoTracks()[0].requestFrame();
        window.requestAnimationFrame(this.captureFrame.bind(this))
    }


