    sortVideoObjectList() {
        this.videoObjectList.sort(function (a, b) {
            if (a.zIndex < b.zIndex) {
                return -1;
            } else if (a.zIndex > b.zIndex) {
                return 1;
            } else {
                if(a.createdAt < b.createdAt) {
                    return -1;
                } else if(a.createdAt > b.createdAt) {
                    return 1;
                } else {
                    return 0;
                }
            }
        })
        this.videoObjectListAsc = [];
        for (let i = this.videoObjectList.length - 1; i >= 0; i--) {
            this.videoObjectListAsc.push(this.videoObjectList[i])
        }
        // console.log(this.videoObjectList)
        // console.log(this.videoObjectListAsc)
    }

