export const SCREEN = {
    width: 1280,
    height: 720,
    // width: 640,
    // height: 360
}

const MARGIN = {
    TOP: 10,
    LEFT: 10,
    RIGHT: 10,
    BOTTOM: 10
}

export const SIZE = {
    SMALL: {
        WIDTH: SCREEN.width / 4,
        HEIGHT: SCREEN.height / 4
    }
}

export const PRESET_POSITION = {
    FULL: {
        top: 0,
        left: 0,
        width: SCREEN.width,
        height: SCREEN.height
    },
    LEFT_FULL: {
        top: 0,
        left: 0,
        width: SCREEN.width / 2,
        height: SCREEN.height
    },
    RIGHT_FULL: {
        top: 0,
        left: SCREEN.width / 2,
        width: SCREEN.width / 2,
        height: SCREEN.height
    },
    TOP_LEFT_SMALL: {
        top: MARGIN.TOP,
        left: MARGIN.LEFT,
        ...SIZE.SMALL
    },
    TOP_RIGHT_SMALL: {
        top: MARGIN.TOP,
        left: getHorizontalRight(SIZE.SMALL.WIDTH),
        ...SIZE.SMALL
    },
    TOP_CENTER_SMALL: {
        top: MARGIN.TOP,
        left: getHorizontalCenter(SIZE.SMALL.WIDTH),
        ...SIZE.SMALL
    },
    BOTTOM_LEFT_SMALL: {
        top: getVerticalBottom(SIZE.SMALL.HEIGHT),
        left: MARGIN.LEFT,
        ...SIZE.SMALL
    },
    BOTTOM_RIGHT_SMALL: {
        top: getVerticalBottom(SIZE.SMALL.HEIGHT),
        left: getHorizontalRight(SIZE.SMALL.WIDTH),
        ...SIZE.SMALL
    },
    BOTTOM_CENTER_SMALL: {
        top: getVerticalBottom(SIZE.SMALL.HEIGHT),
        left: getHorizontalCenter(SIZE.SMALL.HEIGHT),
        ...SIZE.SMALL
    }
}

function getHorizontalRight(objWidth) {
    return SCREEN.width - objWidth - MARGIN.RIGHT;
}

function getHorizontalCenter(objWidth) {
    return (SCREEN.width / 2) - (objWidth / 2);
}

function getVerticalBottom(objHeight) {
    return SCREEN.height - objHeight - MARGIN.BOTTOM;
}

function getVerticalCenter(objHeight) {
    return (SCREEN.height / 2) - (objHeight / 2);
}