export default (function() {
    let _startTime = null;
    let _videoState = {
        videoMixer: null,
        videoElementList: 0,
        droppedFrames: 0,
    }

    function cTime() {
        console.log((`%c[START TIME]%c : ${_startTime}`),
            // 'font-weight: 700;',
            'font-weight: 700; color: white; background: orange;',
            'font-weight: 400; background: none');
        console.log((`%c[CURRENT TIME]%c : ${performance.now()}`),
            // 'font-weight: 700;',
            'font-weight: 700; color: white; background: orange;',
            'font-weight: 400; background: none');
    }

    function cDebug(label, params) {
        console.log(`%c[DEBUG]%c(${label}) : ${params}`,
                    // 'font-weight: 700;',
                    'font-weight: 700; color: white; background: green;',
                    'font-weight: 400; background: none');
    }

    function init(videoElementList, videoMixer) {
        _startTime = performance.now();
        _videoState.videoMixer = videoMixer;
        _videoState.videoElementList = videoElementList
    }

    function showObjectList() {
        cDebug("ObjectList", _videoState.videoElementList)
    }

    let Frames = (function(){
        let _lastExecuteTime = 0;
        let _lastFrameDelay = 0;
        function setLastExecuteTime(time) {
            _lastExecuteTime = time;
        }
        function setLastFrameDelay(time){
            _lastFrameDelay = time;
        }
        function lastDrawTime() {
            cTime();
            cDebug("LastExecuteTime", _lastExecuteTime);
            cDebug("LastFrameDelay", _lastFrameDelay);
            cDebug("Total", _lastExecuteTime + _lastFrameDelay);
        }

        return { setLastExecuteTime, setLastFrameDelay, lastDrawTime}
    })()

    let DroppedFrames = (function() {
        function increase() {
            _videoState.droppedFrames = _videoState.droppedFrames + 1;
        }
        function show() {
            cTime();
            cDebug("DroppedFrames", _videoState.droppedFrames);
            const div = (performance.now() - _startTime) / 1000
            cDebug("DroppedFrames/s", _videoState.droppedFrames / div);
        }

        return { increase, show }
    })()

    return {
        init,
        showObjectList,
        Frames,
        DroppedFrames
    }
})()