import AudioObject, { convertAudioStream } from "./AudioObject";


const setVolume = ({ node, volume }) => {
    const _volume = parseInt(volume, 10) / 100;
    if (_volume < 0 || _volume > 1) throw new Error('volume scope exceed');
    if (!node) throw new Error('node is null');

    // eslint-disable-next-line no-param-reassign
    node.gain.value = _volume;
};

const getVolume = node => {
    return node.gain.value;
};

export default class AudioMixer {
    constructor() {
        const AudioContext = window.AudioContext || window.webkitAudioContext;
        
        this.audioCtx = new AudioContext();
        this.destination = this.audioCtx.createMediaStreamDestination();
        this._isEnable = true;
        this.audioObjectList = [];

        this.addNoiseOscillator();
    }

    addNoiseOscillator() {
        const oscillator = this.audioCtx.createOscillator();
        oscillator.type = 'sine';
        oscillator.frequency.setValueAtTime(24000, this.audioCtx.currentTime); // value in hertz
        oscillator.connect(this.destination);
        oscillator.start();
    }

    getContext() {
        return this.audioCtx;
    }

    getDestination() {
        return this.destination;
    }

    getMasterNode() {
        return this.masterNode;
    }
    

    addAudioObject(aObj) {
        this.audioObjectList.push(
            aObj
        )
    }

    removeAudioObject(id) {
        const idx = this.audioObjectList.findIndex(val => val.id === id);
        if(idx === -1) return;
        const aObj = this.audioObjectList[idx];
        aObj.disconnect();
        this.audioObjectList.splice(idx, 1)
    }

    getAudioTracks() {
        return this.destination.stream.getTracks();
    }
 
    mute(id, value) {
        const idx = this.audioObjectList.findIndex(val => val.id === id);
        const aObj = this.audioObjectList[idx];
        aObj.mute(value);
    }

    setVolume(id, value) {
        const idx = this.audioObjectList.findIndex(val => val.id === id);
        const aObj = this.audioObjectList[idx];
        aObj.setVolume(value)
    }
}
