/** @description src의 형태를 파악하여 스트림으로 변환합니다.
 * @history getStream => convertVideoStream
 * @param {MediaStream|MediaStreamTrack} src MediaStream or MediaStreamTrack
 * @return {MediaStream} stream
 */
export const convertVideoStream = src => {
    const stream = new MediaStream();
// console.log(src instanceof MediaStream);
// console.log(src instanceof MediaStreamTrack)
    if (src instanceof MediaStream) {
        src.getVideoTracks().forEach(track => stream.addTrack(track));
    } else if (src instanceof MediaStreamTrack && src.kind === 'video') {
        stream.addTrack(src);
    } else {
        throw new Error();
    }

    return stream;
};
