import Debugger from './StreamMixer.Debugger';

import AudioMixer from './AudioMixer/AudioMixer';
import AudioObject from './AudioMixer/AudioObject';
import VideoMixer from './VideoMixer/VideoMixer';
import VideoObject from './VideoMixer/VideoObject';

import { SCREEN, PRESET_POSITION } from './VideoMixer/preset/POSITIONS'
// import ModuleToWorkerUrl from './Utils/ModuleToWorkerUrl';
// import { WORKER_MESSAGE_TYPE } from './VideoMixer/VideoObjectWorker';

export default (() => {
    let _videoElementList = [];
    let _videoMixer = null;
    let _audioMixer = null;
    let _previewVideoID = null;
    let _previewVideo = null;
    let _outputStream = null;

    const init = async (canvasElementID, videoElementID) => {
        _previewVideoID = videoElementID;
        _videoMixer = new VideoMixer(canvasElementID);
        // _videoMixer.draw(performance.now());
        // _videoMixer.drawWithoutFps(performance.now());
        // _videoMixer.drawWithFps();
        _videoMixer.drawCalcDeltaTime();
        // _videoMixer.captureFrame();
        _audioMixer = new AudioMixer();

        await initStream();
        // initPreviewVideo(_outputStream);
        Debugger.init(_videoElementList, _videoMixer);
    }

    const initPreviewVideo = (stream) => {
        _previewVideo = document.getElementById(_previewVideoID);
        _previewVideo.srcObject = stream;
        _previewVideo.play()
    }

    /** @description 미디어를 추가합니다. (Promise)
     * @param {object} props {source, sourceType, video:{}, audio{}}
     * source: 미디어 소스 (stream | url)
     * string sourceType: 미디어 소스에 따라 타입 지정 ("stream" | "url")
     * object video: 비디오 위치 및 크기 옵션
     * object audio: 오디오 옵션
     * @return {string} objID
     */
    const addMedia = (props = { source, sourceType: "stream", video: {}, audio: {}, onended}) => {
        return new Promise( async (resolve, reject) => {
            const { source, sourceType } = props;

            const objID = generateID();

            // 공통 비디오 객체 생성후
            const videoEl = document.createElement('video');
            if (sourceType === "url") {
                videoEl.muted = false;
                videoEl.autoplay = true;
                videoEl.src = source;
                videoEl.load();
                if (props.video.startTime) {
                    videoEl.currentTime = props.video.startTime;
                }
                // videoEl.play();
                // videoEl.muted = true;
                videoEl.crossOrigin = 'anonymous';
            } else {
                if(props.audio.mediaType === "mycam")
                    videoEl.muted = true;
                    
                videoEl.srcObject = source;
                if (props.video.startTime) {
                    videoEl.currentTime = props.video.startTime;
                }
            }

            // play 직후 cors오류가 떨어질 수 있기 때문에 promise 타입으로 체크
            videoEl.play()
            .then(() => {

                if (sourceType === "url") videoEl.crossOrigin = 'anonymous';

                if (props.onended) {
                    videoEl.onended = () => {
                        props.onended(objID);
                    }
                }

                _videoElementList.push({
                    id: objID,
                    videoEl,
                    stream: sourceType === 'stream' ? source : null
                })

                const vObj = new VideoObject({
                    id: objID,
                    source: videoEl,
                    sourceType,
                    ...props.video
                });
                _videoMixer.addVideoObject(vObj);

                if(sourceType === "url") {
                    const aObj = new AudioObject({
                        id: objID,
                        source: videoEl,
                        sourceType,
                        context: _audioMixer.getContext(),
                        destination: _audioMixer.getDestination(),
                        ...props.audio,
                    })
                    _audioMixer.addAudioObject(aObj);
                } else {
                    if(source.getAudioTracks && source.getAudioTracks().length !== 0) {
                        const aObj = new AudioObject({
                            id: objID,
                            source: source,
                            sourceType,
                            context: _audioMixer.getContext(),
                            destination: _audioMixer.getDestination(),
                            ...props.audio,
                        })
                        _audioMixer.addAudioObject(aObj);
                    }
                }

                
                resolve({ objID })
            })
            .catch((e) => {
                console.log(e.toString());
                reject({ err: "not play video" })
            });
        })
    }

    const findVideoItem = objID => {
        const idx = _videoElementList.findIndex(k => k.id === objID);
        return _videoElementList[idx];
    }

    const removeMedia = objID => {
        const idx = _videoElementList.findIndex(k => k.id === objID);
        // console.log(_videoElementList[idx].id);
        // console.log(_videoElementList[idx].videoEl)
        if (idx === -1 || !_videoElementList[idx]) {
            console.log(`[StreamMixer] %cNot Found MediaObject : ${objID}`,'background: red; color: #fff')
            return;
        }

        _videoMixer.removeVideoObject(objID);
        _audioMixer.removeAudioObject(objID);

        if (_videoElementList[idx].videoEl)
            _videoElementList[idx].videoEl = undefined;
        _videoElementList.splice(idx, 1);
    }

    const changeStream = (objID, stream) => {
        return new Promise( (resolve, reject) => {
            let item = findVideoItem(objID);
            if(!item) reject("not found mediaObject");
            
            item.videoEl.srcObject = stream;
            item.videoEl.play();
            return resolve();
        })
    }

    const generateID = () => {
        return '_' + Math.random().toString(36).substr(2, 9);
    }

    const initStream = async function() {
        const mixedStream = new MediaStream();

        _videoMixer.getVideoTracks().forEach(function(track) {
            mixedStream.addTrack(track);
        })
        _audioMixer.getAudioTracks().forEach(function(track) {
            mixedStream.addTrack(track);
        })
        _outputStream = mixedStream;
        // const options = {
        //     audioBitsPerSecond : 128000,
        //     videoBitsPerSecond : 2500000,
        //     mimeType : 'video/webm;codecs=h264'
        //   }
        // const mediaRecorder = new MediaRecorder(mixedStream, options);
        // mediaRecorder.start();
        // _outputStream = mediaRecorder.stream;
        // _outputStream = await initPreviewVideo(mixedStream);

    }

    const getStream = function() {
        console.log(_outputStream)
        // 비디오 믹서에 오디오 트랙 합친후 트랙으로 꺼내기
        return _outputStream;
    }


    const getAudioTracks = function() {
        return _audioMixer.getAudioTracks();
    }

    // const mute = function(value) {
    //     _audioMixer.setMute(value);
    // }
    const enableCanvas = function(id, value) {
        _videoMixer.enableCanvas(id, value)
    }

    const pause = function (objID) {
        const videoItem = findVideoItem(objID);
        videoItem.videoEl.pause();
    }

    const play = function(objID) {
        const videoItem = findVideoItem(objID);
        videoItem.videoEl.play();
    }

    const mute = function(id, value) {
        _audioMixer.mute(id, value)
    }

    const getCurrentTime = function(id) {
        const idx = _videoElementList.findIndex(k => k.id === id);
        const item = _videoElementList[idx];
        if(!item) return 0;
        return item.videoEl.currentTime;
    }

    const setCurrentTime = function(id, time) {
        const idx = _videoElementList.findIndex(k => k.id === id);
        const item = _videoElementList[idx];
        if (!item) return;
        item.videoEl.currentTime = time;
    }

    const setPosition = function(values) {
        const vObj = _videoMixer.getVideoObject(values.id);
        if (!vObj) return;
        vObj.setPosition(values)
        _videoMixer.sortVideoObjectList();
    }

    const setDrawMode = function(val) {
        _videoMixer.setDrawMode(val)
    }

    const setVolume = function(id, value) {
        _audioMixer.setVolume(id, value)
    }

    return {
        init,
        addMedia,
        removeMedia,
        changeStream,
        getStream,
        getAudioTracks,
        SCREEN,
        PRESET_POSITION,

        enableCanvas,
        pause,
        play,
        mute,
        setVolume,
        getCurrentTime,
        setCurrentTime,
        setPosition,

        setDrawMode,

        Debug: {...Debugger}
    }
    
})()