export const convertAudioStream = src => {
    const stream = new MediaStream();
    
    if (src instanceof MediaStream) {
        src.getAudioTracks().forEach(track => stream.addTrack(track));
    }
    else if (src instanceof MediaStreamTrack) {
        stream.addTrack(src);
    }
    // else throw new Error();

    return stream;
};

export default class AudioObject {
    constructor({
        id,
        source = null,
        sourceType = "stream",
        context,
        destination,
        mediaType,
        muted,
    }) {
        this._id = id;
        this._source = source;
        this._sourceType = sourceType;
        // this._source = convertAudioStream(source);
        this._audioCtx = context;
        this._destination = destination;

        this._mediaType = mediaType;
        this._muted = muted;

        // this._filteredNode = this._audioCtx.createGain();
        // this._filteredNode.gain.value = 1.0;
        if(this._sourceType === "stream") {
            this._sourceNode = this._audioCtx.createMediaStreamSource(this._source);
        } else {
            this._sourceNode = this._audioCtx.createMediaElementSource(this._source);
        }

        this._gainNode = this._audioCtx.createGain();

        if (mediaType === "mycam") {
            this._gainNode.gain.value = 0.5;
            // voice bandpass filter
            const filter = this._audioCtx.createBiquadFilter()
            var geometricMean = Math.sqrt(300 * 3400);
            filter.frequency.value = geometricMean;
            filter.Q.value = geometricMean / (300 - 3400);

            // if mic node is source then -> filter -> gain -> dest
            this._sourceNode.connect(filter);
            filter.connect(this._gainNode);
        } else {
            this._gainNode.gain.value = 1.0;
            // if mic node is not source then -> gain -> dest
            this._sourceNode.connect(this._gainNode);
        }

        this._gainNode.connect(this._destination);


        // this._audio.play();
        // console.log(this._destination)

        // this._gainNode = this._audioCtx.createGain();
        // this._gainNode.gain.value = 1.0;

        // this._sourceNode.connect(this._gainNode);
// console.log("audio type", type)
//         if (type === "mic") {
//             const lowpassFilterNode = this._audioCtx.createBiquadFilter();
//             lowpassFilterNode.type = 'lowpass';
//             lowpassFilterNode.frequency.setValueAtTime(100, this._audioCtx.currentTime);

//             const highpassFilterNode = this._audioCtx.createBiquadFilter();
//             highpassFilterNode.type = 'highpass';
//             highpassFilterNode.frequency.setValueAtTime(1500, this._audioCtx.currentTime);

//             this._gainNode.connect(lowpassFilterNode);
//             lowpassFilterNode.connect(highpassFilterNode);
//             highpassFilterNode.connect(this._filteredNode);
//         } else {
//             this._gainNode.connect(this._filteredNode)
//         }
    }

    get id() {
        return this._id;
    }

    disconnect() {
        this._gainNode.disconnect();
        this._sourceNode.disconnect();
    }

    mute(value) {
        if(value) {
            this._gainNode.gain.setValueAtTime(0, this._audioCtx.currentTime);
        } else {
            // this._sourceNode.connect(this._gainNode);
            // this._gainNode.connect(this._destination);

            // if (this._mediaType === "mic") {
            //     this._gainNode.gain.setValueAtTime(0.5, this._audioCtx.currentTime);
            // } else {
            //     this._gainNode.gain.setValueAtTime(1.0, this._audioCtx.currentTime);
            // }
            this._gainNode.gain.setValueAtTime(1.0, this._audioCtx.currentTime);
        }
    }

    setVolume(value) {
        this._gainNode.gain.setValueAtTime(value, this._audioCtx.currentTime);
    }

}